import React, {useState} from 'react';
import hand from "../hand.svg";
import mailclosed from "../mailclosed.svg";
import mailopen from "../mailopen.svg";
import Link from "./link";

const Mailman = () => {
  const [img, setImg] = useState(mailclosed);
  
  const handleMouse = () => {
    setImg(mailopen)
  }
  
  const handleMouseOut = () => {
    setImg(mailclosed)
  }
  return (
    <div style={{display: 'flex', justifyContent: 'center'}}>
      <img src={hand} style={{width: 40}} alt=""/>
      <Link
        to="mailto:dev.maximocampo@gmail.com"
        onMouseEnter={handleMouse}
        onMouseLeave={handleMouseOut}
        style={{width: 80, textAlign: 'right', marginRight: 10, cursor: 'pointer',}}
      >
        <img src={img} style={{height: 60}} alt=""/>
      </Link>
      <img src={hand} style={{width: 40, transform: 'scaleX(-1)'}} alt=""/>
    </div>
  );
};

export default Mailman;
